.timeline {
    margin: 0 auto;
    max-width: 500px;
    padding: 20px;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica,
    Arial, sans-serif;
}
body {
    color: #262626;
    font-size: 14px;
    line-height: 18px;
}
.hugi {
    background-color: #fafafa;
    min-height: 100vh;
}
.hugi_header {
    position: sticky;
    top: 0;
    z-index: 9999;
    background-color: white;
    height: 54px;
    display: flex;
    align-items: center;
    object-fit: contain;
    border-bottom: 1px solid lightgray;
}
.hugi_headerWrapper {
    max-width: 975px;
    margin: 0 auto;
    width: 100%;
    padding: 0 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.hugi_headerButtons {
    display: flex;
    column-gap: 15px;
}
button {
    font-size: 14px;
    line-height: 18px;
    cursor: pointer;
    border: 1px solid transparent;
}
.primary_button {
    background-color: #0095f6;
    border-radius: 4px;
    color: #fff;
    padding: 5px 9px;
    font-weight: 600;
    text-align: center;
}
.text_button {
    color: #0095f6;
    font-weight: 600;
    line-height: 28px;
    background-color: transparent;
}
.hugi_signUp {
    display: flex;
    flex-direction: column;
    margin: 28px 0;
    row-gap: 15px;
}